<template>
  <div class="mallList">
    <div class="head_nav">
      <van-search class="head_search" v-model="search" placeholder="请输入搜索关键词">
        <template #left-icon>
          <van-icon class-prefix="iconfont iconsousuo" name="iconsousuo" />
        </template>
      </van-search>
    </div>
    <div class="content_main">
      <div class="tabs_item" id="item_i">
        <div class="item_right">
          <div class="r_line"></div>
          <div class="r_iconfont" @click="show=true">
            <van-icon name="filter-o" />
          </div>
        </div>
      </div>
      <van-tabs
        v-model="active"
        id="tabs"
        title-active-color="#7c322f"
        color="#7c322f"
        line-height="2"
        title-inactive-color="#333333"
        swipeable
      >
        <van-tab title="综合">
          <div class="c_item" @click="goPath('goodsInfo')">
            <div class="c_item_image"></div>
            <div class="c_item_text">
              <div class="c_item_text_head">
                <div class="c_item_text_head_title">宁夏枸杞</div>
                <div class="c_item_text_head_tag">
                  <i class="iconitem iconfont icontianjia"></i>
                </div>
              </div>
              <div class="c_item_text_describe">
                <span class="c_item_text_describe_left">¥69.9</span>
                <span class="c_item_text_describe_right">包邮</span>
              </div>
            </div>
          </div>
          <div class="c_item">
            <div class="c_item_image"></div>
            <div class="c_item_text">
              <div class="c_item_text_head">
                <div class="c_item_text_head_title">苍术（已炒制）</div>
                <div class="c_item_text_head_tag">
                  <i class="iconitem iconfont icontianjia"></i>
                </div>
              </div>
              <div class="c_item_text_describe">
                <span class="c_item_text_describe_left">¥69.9</span>
                <span class="c_item_text_describe_right">包邮</span>
              </div>
            </div>
          </div>
          <div class="c_item">
            <div class="c_item_image"></div>
            <div class="c_item_text">
              <div class="c_item_text_head">
                <div class="c_item_text_head_title">苍术（已炒制）</div>
                <div class="c_item_text_head_tag">
                  <i class="iconitem iconfont icontianjia"></i>
                </div>
              </div>
              <div class="c_item_text_describe">
                <span class="c_item_text_describe_left">¥69.9</span>
                <span class="c_item_text_describe_right">包邮</span>
              </div>
            </div>
          </div>
        </van-tab>
        <van-tab title="销量"></van-tab>
        <van-tab title="新品"></van-tab>
        <van-tab title="价格"></van-tab>
      </van-tabs>
    </div>
    <van-popup v-model="show" position="right" :style="{ width: '75%',height:'100%' }">
      <div class="popup_body">
        <div class="header">
          <span>筛选</span>
        </div>
        <div class="content">
          <div class="title">折扣和服务</div>
          <div class="tag-wrapper">
            <div class="tag">
              <span>包邮</span>
            </div>
            <div class="tag">
              <span>消费者保障</span>
            </div>
            <div class="tag">
              <span>7+7天退货</span>
            </div>
            <div class="tag">
              <span>活动</span>
            </div>
            <div class="tag">
              <span>优惠</span>
            </div>
          </div>
          <div class="title">价格区间(元)</div>
          <div class="interval-wrapper">
            <div class="interval">
              <div>
                <div class="tag">
                  <interval-input width="45px" type="text" v-model="form.min" placeholder="最低价" />
                </div>
              </div>
              <div>
                <div class="line"></div>
              </div>
              <div>
                <div class="tag">
                  <interval-input width="45px" type="text" v-model="form.max" placeholder="最高价" />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="footer">
          <div class="reset">重置</div>
          <div class="complete" @click="save">完成</div>
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
export default {
  data() {
    return {
      show: false,
      data: [],
      search: "",
      active: 0,
      form: {},
    };
  },
  mounted() {},
  methods: {
    goPath(name) {
      this.$router.push({ name });
    },
    save() {
      console.log(this.form);
    },
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/styles/global.scss";
.mallList {
  width: 100%;
  min-height: 100vh;
  background-color: #fff;
  .head_nav {
    position: relative;
    &::after {
      position: absolute;
      box-sizing: border-box;
      content: " ";
      pointer-events: none;
      top: -50%;
      right: -50%;
      bottom: -50%;
      left: -50%;
      border-bottom: 1px solid #ebedf0;
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }
    .head_search {
      width: 100%;
      padding: 10px 18px;
      .van-search__content {
        border: solid 1px #7e3330;
        border-radius: 20px;
        background-color: #fff;
        /deep/ .van-cell {
          font-size: 14px;
          padding: 5px 8px 5px 0;
          .van-field__left-icon {
            height: 100%;
            i {
              display: flex;
              box-sizing: border-box;
              font-size: 20px;
            }
          }
        }
      }
    }
  }
  .content_main {
    width: 100%;
    height: auto;
    background-color: #fff;
    .tabs_item {
      float: right;
      width: 35%;
      height: 40px;
      background: #fff;
      overflow: hidden;
      text-align: right;
      .item_right {
        width: 60px;
        padding: 0 10px;
        height: 100%;
        display: inline-flex;
        align-items: center;
        justify-content: space-between;
        .r_line {
          width: 2px;
          height: 20px;
          background: rgba(204, 204, 204, 0.6);
        }
        .r_iconfont {
          display: inline-block;
          vertical-align: middle;
          i {
            font-size: 25px;
            color: #646566;
          }
        }
      }
    }
    /deep/ .van-tabs {
      pointer-events: none;
      .van-tabs__wrap {
        pointer-events: all;
        width: 65%;
        height: 40px;
      }
      .van-tabs__wrap::after {
        border-width: 0;
      }
      .van-tabs__content {
        pointer-events: all;
        width: 100%;
        height: auto;
        .van-tab__pane {
          width: 100%;
          height: auto;
          margin-top: 15px;
          padding: 0 15px;
          background-color: #fff;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          .c_item {
            width: 49%;
            margin-bottom: 4%;
            height: 200px;
            border-radius: 5px;
            overflow: hidden;
            // border: solid 1px #dcdcdc;
            .c_item_image {
              width: 100%;
              height: 65%;
              background-color: #f7f3ed;
            }
            .c_item_text {
              width: 100%;
              height: 35%;
              padding: 10px;
              border-bottom: 1px solid #dcdcdc;
              border-right: 1px solid #dcdcdc;
              border-left: 1px solid #dcdcdc;
              border-radius: 0px 0px 5px 5px;
              .c_item_text_head {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 5px;
                .c_item_text_head_title {
                  font-size: 14px;
                }
                .c_item_text_head_tag {
                  display: inline-flex;
                  align-items: center;
                  .iconitem {
                    font-size: 25px;
                    color: #7e3330;
                  }
                }
              }
              .c_item_text_describe {
                display: flex;
                justify-content: space-between;
                align-items: flex-end;
                .c_item_text_describe_left {
                  color: #79312e;
                }
                .c_item_text_describe_right {
                  color: #666666;
                  font-size: 12px;
                }
              }
            }
          }
        }
      }
    }
  }
  .popup_body {
    width: 100%;
    height: 100%;
    font-size: 14px;
    color: #333333;
    > div {
      box-sizing: border-box;
    }
    > .header {
      padding: 0 15px;
      width: 100%;
      height: 40px;
      line-height: 40px;
      background-color: #eeeeee;
      text-align: left;
    }
    > .content {
      padding: 0 15px;
      width: 100%;
      height: calc(100vh - 120px);
      .title {
        padding: 15px 0;
      }
      .tag-wrapper {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        > div {
          @include center;
          width: 30%;
          height: 30px;
          padding: 0 4px;
          background: #ffffff;
          border: 1px solid #a7a7a7;
          border-radius: 5px;
          margin-bottom: 15px;
          > span {
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
          }
        }
      }
      .interval-wrapper {
        width: 100%;
        .interval {
          display: flex;
          > div {
            flex: 1;
          }
          > div:nth-of-type(2) {
            @include center;
          }
          .tag {
            width: 100%;
            padding: 5px 0;
            @include center;
            background: #eeeeee;
            border-radius: 27px;
          }
          .line {
            width: 75%;
            height: 1px;
            background: #999999;
          }
        }
      }
    }
    > .footer {
      padding: 0 15px;
      width: 100%;
      height: 80px;
      font-size: 16px;
      color: #fff;
      &::after {
        content: "";
        clear: both;
      }
      > div {
        padding: 0.5rem 15.5%;
      }
      .reset {
        background: #7e3330;
        border-radius: 36px;
        float: left;
      }
      .complete {
        background: linear-gradient(90deg, #c8a060, #e6c46a);
        border-radius: 36px;
        float: right;
      }
    }
  }
}
</style>


